        const currentReport = report && report.data ? JSON.stringify(report.data) : '';
        const previousReport = prevProps.report && prevProps.report.data ? JSON.stringify(prevProps.report.data) : '';
        if (previousReport !== currentReport) {
            this.initDatum();
        }
